/*==================================
    TIMELINE
==================================*/

$primary-color: #ee1c25;
.timeline {
  line-height: 1.4em;
  list-style: none;
  margin: 0;
  padding: 0;
  width: 100%;
}

/*----- TIMELINE ITEM -----*/

.timeline-item {
  padding-left: 20px;
  position: relative;
}

/*----- TIMELINE INFO -----*/

.timeline-info {
  font-size: 12px;
  font-weight: 700;
  letter-spacing: 2px;
  margin: 0 0 0.1em 0;
  text-transform: uppercase;
  white-space: nowrap;
  span {
    padding-left: 1px;
  }
}
/*----- TIMELINE MARKER -----*/

.timeline-marker {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  width: 15px;
  &:before {
    background: $primary-color;
    border: 3px solid transparent;
    border-radius: 100%;
    content: "";
    display: block;
    height: 15px;
    position: absolute;
    top: 4px;
    left: 0;
    width: 15px;
  }
  &:after {
    content: "";
    width: 3px;
    background: #ccd5db;
    display: block;
    position: absolute;
    top: 24px;
    bottom: 0;
    left: 6px;
  }
  .timeline-item:last-child &:after {
    content: none;
  }
}

.intermediate-points:before {
  background: transparent;
  border: 3px solid $primary-color;
}

.last-stop:before {
  background: transparent;
  border: none;
  content: "\f041";
  font-family: FontAwesome;
  display: block;
  position: absolute;
  top: 4px;
  left: 0px;
  width: 15px;
  font-size: 26px;
}

.timeline-content {
  padding-bottom: 40px;
}
.pay-integration p {
  margin: 0;
}

