.wp-common-block-bus {
  margin-bottom: 50px;
}

.bus-heading {
  margin: 40px 0 35px;
  padding: 0;
  span {
    font-size: 30px;
    font-weight: 600;
    margin-top: 0;
    text-transform: capitalize;
    font-family: "Josefin Sans", sans-serif;
    margin-bottom: 0.25em;
    color: #334e6f;
  }
}

@media only screen and (min-width: 1024px) {
  .bus-heading span {
    font-size: 40px;
  }
}

.bus-slider {
  .tr-single-body {
    padding: 15px 25px 0px;
    .third {
      margin-bottom: 0;
      font-weight: 600;
      li {
        width: 50%;
      }
    }
  }
}

@media (max-width: 400px) {
  .bus-slider .tr-single-body .third li {
    width: 100%;
  }
}

.bus-slider #carousel {
  box-shadow: 0 0 18px 0 rgba(0, 5, 10, 0.55);
  margin: 0;
}

.bus-slider #thumbcarousel .item {
  text-align: center;
}

.bus-slider #thumbcarousel .item .thumb {
  /*border: 1px solid #cecece;*/
  width: 23.5%;
  margin: 0 0 5px 0;
  display: inline-block;
  vertical-align: middle;
  cursor: pointer;
  /*max-width: 100px; */
  &:first-child {
    margin-left: 0;
  }
  &:last-child {
    margin-right: 0;
  }
}

.bus-slider .item img {
  width: 100%;
  height: auto;
}

.bus-info-slider {
  box-shadow: 0 0 18px 0 rgba(0, 5, 10, 0.55);
  margin-bottom: 0;
  .tr-single-header {
    padding: 17px 25px 12px;
  }
}

.wrapper-bus-info {
  border-top: 1px solid #eee;
  padding: 10px 25px 10px;

  p {
    margin: 0;
  }
}

.separation-unit {
  text-align: center;
}

.spacer {
  position: relative;

  .mask {
    overflow: hidden;
    height: 10px;
    &:after {
      content: "";
      display: block;
      margin: -25px auto 0;
      width: 100%;
      height: 25px;
      border-radius: 125px / 12px;
      box-shadow: 0 0 8px black;
    }
  }
}

/* Bus End*/

/* navbar-phones */
.wp-left-dropdown-menu {
  position: absolute;
  top: -110%;
  left: 0;
}

.wp-right-dropdown-menu {
  position: absolute;
  top: -110%;
  right: 0;
}

.wp-mini-center,
.wp-left-dropdown-menu,
.wp-right-dropdown-menu {
  .dropdown {
    font-family: "Montserrat", sans-serif;
    font-weight: 500;
    position: relative;
    font-size: 14px;
    color: white;
  }

  .dropdown-content {
    display: none;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    padding: 0 0 0 0;
    margin-top: 20px;
  }

  .dropdown:hover .dropdown-content {
    display: block;
  }

  .dropdown-itens {
    color: inherit;
    text-transform: capitalize;
    text-decoration: none;
    display: block;
    text-align: left;
    margin: 1em 0;
    min-width: 190px;
    .img-flag-phones {
      display: inline-block;
      padding-right: 8px;
      margin-top: -1px;
    }
    i {
      margin-left: 5px;
    }
  }
}

@media (min-width: 993px) {
  .wp-left-dropdown-menu,
  .wp-right-dropdown-menu {
    top: -90%;
  }
}

@media (max-width: 767.98px) {
  .multi-option-booking {
    .fl-wrap {
      text-align: center;
    }
  }
  .wp-left-dropdown-menu,
  .wp-right-dropdown-menu {
    display: none;
  }

  .wp-mini-center {
    top: -30px;
    background: transparent;
    color: #ffffff;
    width: auto !important;
    max-width: 250px !important;
    float: none;
    display: inline-block;
    clear: both;
  }
  .list {
    color: #6b7c8a;
    font-weight: 500;
    li {
      margin-top: -8px;
    }
    a {
      cursor: pointer;
      font-weight: 500;
      line-height: 20px;
      list-style: none;
      padding-left: 18px;
      padding-right: 29px;
    }
  }
}

@media screen and (min-width: 768px) {
  .wp-mini-center {
    display: none;
  }
}

/* navbar-phones End*/

